.ScoreTable {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    font-size: 0.9rem;
    width: 90%;
    background-color: white;
    padding: 1em;
}

.ScoreTable table {
    width: 100%;
    margin: 0 auto;
    min-width: 400px;
    font-weight: 600;
}

.ScoreTable-section {
    gap: 10px;
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    vertical-align: top;
    -ms-flex-direction: column;
        flex-direction: column;
}

.ScoreTable h2 {
    font-weight: 600;
    font-size: 2em;
    -webkit-margin-before: 0px !important;
            margin-block-start: 0px !important;
    -webkit-margin-after: 0px !important;
            margin-block-end: 0px !important;
    -webkit-margin-start: 0px !important;
            margin-inline-start: 0px !important;
    -webkit-margin-end: 0px !important;
            margin-inline-end: 0px !important;
}

.total-score {
    width: 100% !important;
    left: 10px;
    translate: -10px;
    font-size: 20px;
    font-weight: 600;
    margin-top: 20px;
    background-color: #7335b5;
    padding-block: 10px;
    cursor: default;
    border-radius: 5px;
    color: white;
}

.RuleRow {
    font-size: 17px;
}



