* {
    padding: 0;
    margin: 0;
    position: relative;
}

body {
    width: 100%;
    height: 100vh;
    font-weight: 100;
    font-family: "Roboto";
    background: -webkit-gradient(linear, left top, right top, from(#91eae4), color-stop(#86a8e7), to(#7f7fd5));
    background: -webkit-linear-gradient(left, #91eae4, #86a8e7, #7f7fd5);
    background: -o-linear-gradient(left, #91eae4, #86a8e7, #7f7fd5);
    background: linear-gradient(to right, #91eae4, #86a8e7, #7f7fd5);
}

@media (max-width: 650px) {
    body {
        background: -webkit-gradient(linear, left top, right top, from(#ffffff), color-stop(#ffffff), to(#ffffff));
        background: -webkit-linear-gradient(left, #ffffff, #ffffff, #ffffff);
        background: -o-linear-gradient(left, #ffffff, #ffffff, #ffffff);
        background: linear-gradient(to right, #ffffff, #ffffff, #ffffff)
    }
}




